<template>
  <div>
   <h1>REV余额查询工具</h1>
   <h3 class="my-dan">不要提供助记词</h3>
   <h3 class="my-dan">不要提供私钥</h3>

  <div  class="my-container">
    <div class="my-elm">ETH地址<span class="my-info">自动转REV地址</span></div> 
    <van-field
        clearable
        v-model="inputEthAddress"
        name="ETH地址"    
        placeholder="请输入ETH地址"
        :rules="[{ required: true, message: '请输入ETH地址' }]"
    /> 
    <van-button @click="checkBalanceFromEthAddress" type="info">查询</van-button>    
  </div>


    <div  class="my-container">
    <div class="my-elm">REV地址</div> 
    <van-field
        clearable
        v-model="inputRevAddress"
        name="REV地址"    
        placeholder="请输入REV地址"
        :rules="[{ required: true, message: '请输入REV地址' }]"
    /> 
    <van-button @click="checkBalanceFromRevAddress" type="danger">查询</van-button>    
  </div>

   
<div  class="my-container">
 <div class="my-elm">查询结果</div>
 <div class="my-balance">
      {{resultRevBalance}}
 </div>
 <div class="my-address">
     {{resultRevAddress}}
 </div>
</div>




    </div>
</template>

<script>
import { getAddrFromEth,toBase58Fun } from "@/utils/addressUtils";
import { async } from "q";
import { Dialog } from 'vant';
import { Notify } from 'vant';
export default {
  name: "Home",
  data() {
    return {      
      inputEthAddress:'',
      inputRevAddress:'',
      resultRevAddress:'',
      resultRevBalance:'',
      currentRate:100
    };
  },
  methods: {

      checkBalanceFromRevAddress(){
        this.resultRevAddress=""
        this.resultRevBalance=""
        let address = this.inputRevAddress.trim()
        if (address == 'undefined' || !address || !/[^\s]/.test(address)) {
            //为空
            Notify({ type: 'warning', message: '请输入有效REV地址.' });
            return;
        }    

        console.log(address)

        this.checkBalance(address)
      },
     checkBalanceFromEthAddress(){ 

         this.resultRevAddress=""
         this.resultRevBalance=""

        let address=this.inputEthAddress.trim();

         if (address == 'undefined' || !address || !/[^\s]/.test(address)) {
            //为空
            Notify({ type: 'warning', message: '请输入有效ETH地址.' });
            return;
        }    

        if(address.startsWith("0x") || address.startsWith("0X")){
            address=address.substring(2,address.length);
        }

        if(address.length!=40){
            Notify({ type: 'warning', message: '请输入有效ETH地址.' });
            return;
        }

        this.resultRevAddress=getAddrFromEth(address)

        console.log( this.resultRevAddress)

        this.checkBalance(this.resultRevAddress)
    },      
    checkBalance: async function(address) {
      const url = "http://35.220.140.14:40403/api/explore-deploy";

      const req = {
        method: "POST",
        body: `
              new return, rl(\`rho:registry:lookup\`), RevVaultCh, vaultCh in {
                rl!(\`rho:rchain:revVault\`, *RevVaultCh) |
                for (@(_, RevVault) <- RevVaultCh) {
                  @RevVault!("findOrCreate", "${address}", *vaultCh) |
                  for (@maybeVault <- vaultCh) {
                    match maybeVault {
                      (true, vault) => @vault!("balance", *return)
                      (false, err)  => return!(err)
                    }
                  }
                }
              }
            `
      };
    
      await fetch(url, req).then(r =>r.json()).then(x=>{
          console.log(x)
          this.resultRevBalance=x.expr[0].ExprInt.data/100000000
          this.resultRevAddress="REV地址:"+address;
      }).catch(err=>{
          Dialog.alert({
              message:'出现错误,请重试.'
          })
      })    
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.my-container{
    display: flex;
    flex-direction:column;
    width:100%;
    padding:1rem;
    text-align: letf;
}

.my-elm{
    display: flex;
    flex-direction:column;
    color:black;
    text-align: letf;
    width:100%;
}

.my-info{
    color:gray;
    font-size:0.8rem;
}

.my-address{
    font-size:0.8rem;
}

.my-balance{
    color:coral;
    font-size:4rem;
}

.my-dan{
    color:crimson;
}
</style>



